/**
 * 所有元素继承html的box-sizing
 */
* {
  box-sizing: inherit;
}
/**
 * 重定义html的box-sizing
 */
html {
  box-sizing: border-box;
}

html, body {
  height: 100%;
}

body {
  margin: 0 auto;
  min-width: 1060px;
  background: #f3f3f3;
  //font: 1.125em / 1.45;
}
.layout-bg{
  width:1220px;
  margin:0 auto;
  background:#fff;
  padding:20px;
  margin-bottom:20px;
}

/***
* 清除浮动
*/
.clearfix:after {
  font-size: 0;
  line-height: 0;
  content: "\020";
  display: block;
  height: 0;
  clear: both;
}

.clearfix {
  zoom: 1;
  overflow: hidden;
}

:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
  color: #ccc;
}

::-moz-placeholder { /* Mozilla Firefox 19+ */
  color: #ccc;
}

input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
  color: #ccc;
}

input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  color: #ccc;
}
* {
  margin: 0;
  padding: 0;
  list-style: none;
}

/*
KISSY CSS Reset
理念：1. reset 的目的不是清除浏览器的默认样式，这仅是部分工作。清除和重置是紧密不可分的。
2. reset 的目的不是让默认样式在所有浏览器下一致，而是减少默认样式有可能带来的问题。
3. reset 期望提供一套普适通用的基础样式。但没有银弹，推荐根据具体需求，裁剪和修改后再使用。
特色：1. 适应中文；2. 基于最新主流浏览器。
维护：玉伯<lifesinger@gmail.com>, 正淳<ragecarrier@gmail.com>
 */

/** 清除内外边距 **/
body, h1, h2, h3, h4, h5, h6, hr, p, blockquote, /* structural elements 结构元素 */
dl, dt, dd, ul, ol, li, /* list elements 列表元素 */
pre, /* text formatting elements 文本格式元素 */
form, fieldset, legend, button, input, textarea, /* form elements 表单元素 */
th, td /* table elements 表格元素 */
{
  margin: 0;
  padding: 0;
}

/** 设置默认字体 **/
body,
button, input, select, textarea /* for ie */
{
  font: 14px/1.5 'Microsoft YaHei',"\5FAE\x8F6F\6B63\9ED1\4F53 ","华文黑体","STHeiti","思源黑体","Noto Sans CJK SC","方正黑体","宋体","SimSun",regular,"\5FAE\8F6F\96C5\9ED1","\9ED1\4F53","\5b8b\4f53",'Hiragino Sans GB', tahoma, helvetica, arial;
}
input{
  font-family:Arial,'Microsoft YaHei',"\5FAE\x8F6F\6B63\9ED1\4F53","华文黑体","STHeiti","思源黑体","Noto Sans CJK SC","方正黑体","宋体","SimSun";
  outline:none;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
}

address, cite, dfn, em, var {
  font-style: normal;
}

/* 将斜体扶正 */
code, kbd, pre, samp {
  font-family: courier new, courier, monospace;
}

/* 统一等宽字体 */
small {
  font-size: 14px;
}

/* 小于 12px 的中文很难阅读，让 small 正常化 */

/** 重置列表元素 **/
ul, ol {
  list-style: none;
}

/** 重置文本格式元素 **/
a {
  text-decoration: none;
  color: #313131;
}

a:hover {
  color: #313131;
}

/** 重置表单元素 **/
legend {
  color: #000;
}

/* for ie6 */
fieldset, img {
  border: 0;
}

/* img 搭车：让链接里的 img 无边框 */
button, input, select, textarea {
  font-size: 100%;
}

/* 使得表单元素在 ie 下能继承字体大小 */
/* 注：optgroup 无法扶正 */

/** 重置表格元素 **/
table {
  border-collapse: collapse;
  border-spacing: 0;
}
input::-ms-clear,input::-ms-reveal{display:none;}