$font16:16px;
$font18:18px;
$font20:20px;
@mixin magrin0{
  margin: 0 auto;
}
@mixin font0{
  font-size: 0;
}
@mixin radius($value) {
  -moz-border-radius: $value;
  -webkit-border-radius: $value;
  border-radius: $value;
}
@mixin float($direction: left,$support-for-ie:false){
  float: unquote($direction);
  @if $support-for-ie{
    _display: inline;
  }
}