.mainHome-header{
	width: 100%;
	height: 100px;
	background-color: #fff;
	.wraper1200{
		width: 1200px;
		height: 100px;
		margin: 0 auto;
		.logo{
			background: url(../static/img/home-logo.png) no-repeat;
			width: 473px;
			height: 64px;
			margin-top: 18px;
			display: inline-block;
		}
		.client{
			text-align: right;
			margin-top: 15px;
			margin-bottom: 22px;
			.party-client{
				margin-right: 40px;
				display: inline-block;
				color: #555;
				font-size: 12px;
				i{
					background: url(../static/img/party-client.png) no-repeat;
					width: 11px;
					height: 16px;
					margin-right: 10px;
					display: inline-block;
					vertical-align: middle;
				}
			}
			.mediator-client{
				color: #555;
				font-size: 12px;
				i{
					background: url(../static/img/mediator-client.png) no-repeat;
					width: 13px;
					height: 17px;
					margin-right: 10px;
					display: inline-block;
					vertical-align: middle;
				}
			}
		}
		.roles{
			display: inline-block;
			ul{
				li{
					float: left;
					padding: 0 26px;
					border-right: 1px solid #cfcfcf;
					&.last-li{
						border-right: none;
						padding-right: 0;
					}
					a{
						font-size: 14px;
						color: #333;
					}
				}
			}
		}
	}
}
